body {

	// Por defecto todos los UI Elements ocultos
	.ui-element {
		display: none;
	}

	// Muestra el formulario
	&.form-shown {
		.add-icon {
			transform: rotate(135deg);
		}
		.new-song-form {
			display: flex;
		}
		@include if-is-tablet {
			.songs-list {
				display: flex;
			}
		}
	}

	// Muestra el listado de canciones
	&.songs-list-shown .songs-list {
		display: block;
		@include if-is-tablet {
			display: flex;	
		}
	}

}
